
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import VHtmlPanel from '@/components/HtmlPanel.vue'
export default {
components: {
Scroller,
Divider,
Spinner,
XButton,
Group,
Cell,
LoadMore,
VHtmlPanel
},
data () {
return {
url1: '',
showList1: true,
scrollTop: 0,
onFetching: false,
bottomCount: 20
}
},
mounted () {

this.url1 = 'https://www.baidu.com/'

this.$nextTick(() => {
this.$refs.scrollerEvent.reset({top: 0})
})
this.$nextTick(() => {
this.$refs.scrollerBottom.reset({top: 0})
})
},
methods: {
onScrollBottom () {
if (this.onFetching) {

} else {
this.onFetching = true
setTimeout(() => {
this.bottomCount += 10
this.$nextTick(() => {
this.$refs.scrollerBottom.reset()
})
this.onFetching = false
}, 2000)
}
},
onScroll (pos) {
this.scrollTop = pos.top
},
onCellClick () {
window.alert('cell click')
},
onClickButton () {
window.alert('click')
},
changeList () {
this.showList1 = false
this.$nextTick(() => {
this.$refs.scroller.reset({
top: 0
})
})
}
}
}



