export default{
 //  :url.sync=""
 props: {
  url: {
   required: true
  }
 },
 data () {
  return {
   loading: false,
   html: ''
  }
 },
 watch: {
  url (value) {
   this.load(value)
  }
 },
 mounted () {
  this.load(this.url)
 },
 methods: {
  load (url) {
   if (url && url.length > 0) {
    // 
    this.loading = true
    let param = {
     accept: 'text/html, text/plain'
    }
    this.$http.get(url, param).then((response) => {
     this.loading = false
     // HTML
     this.html = response.data
    }).catch(() => {
     this.loading = false
     this.html = ''
    })
   }
  }
 }
}



